// Dropdowns
// -----------------------------------------------------------------------------
$dropdown-item-font-size: .9375rem !default;
$dropdown-item-transition: background-color $transition-duration $ease-in-out-circ, color $transition-duration $ease-in-out-circ !default;

// Small dropdowns
$dropdown-small-padding-y: .25rem !default;
$dropdown-small-font-size: 0.813rem !default;
$dropdown-small-box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, .11), 0 3px 10px rgba(0, 0, 0, .05), 0 2px 3px rgba(0, 0, 0, .06) !default;

$dropdown-small-item-padding-y: 0.375rem !default;
$dropdown-small-item-padding-x: 0.875rem !default;

$dropdown-small-item-font-size: 0.813rem !default;

$dropdown-small-divider-margin-x: 0 !default;
$dropdown-small-divider-margin-y: 0.25rem !default;


// The dropdown menu
.dropdown-menu {
  z-index: $zindex-dropdown;
  min-width: $dropdown-min-width;
  padding: $dropdown-padding-y 0;
  margin: $dropdown-spacer 0 0;
  font-size: $font-size-base;
  color: $body-color;
  background-color: $dropdown-bg;
  border: $dropdown-border-width solid $dropdown-border-color;
  @include border-radius($dropdown-border-radius);
  @include box-shadow($dropdown-box-shadow);

  &-small {
    @include box-shadow($dropdown-small-box-shadow);
    padding: $dropdown-small-padding-y 0;
    font-size: $dropdown-small-font-size;

    .dropdown-item {
      padding: $dropdown-small-item-padding-y $dropdown-small-item-padding-x;
      font-size: $dropdown-small-item-font-size;
    }

    .dropdown-divider {
      margin: $dropdown-small-divider-margin-y $dropdown-small-divider-margin-x;
    }
  }
}

select[multiple] ~ .btn-group{
  display: flex;
  label {
    padding: 3px 20px;
  }
  .multiselect-container {
    width: 100%;
  }

  button.multiselect {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }
  
  &.filter-slider {
    @include media-breakpoint-down(md) {
      .dropdown-menu {
        transform: translate3d(500px,0,0) !important;
        height: 100vh;
        position: fixed !important;
        width: calc(100vw - 35px);
        border-radius: 0;
        right: 0;
        left: inherit !important;
        transition: transform 300ms cubic-bezier(0.4, 0, 0.6, 1);
      }
      &::after {
        content: "";
        position: fixed;
        width: 100%;
        height: 100%;
        background: transparent;
        display: block;
        top: 0;
        left: -100%;
        z-index: 999;
        opacity: .8;
        transition: background-color 400ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
      }
      &.show {
        .dropdown-menu.show {
          padding: 30px 0;
          transform: translate3d(0,0,0) !important;
        }
        &::after {
          left: 0;
          background: $black;
        }
      }
    }
  }

}
.dropup .dropdown-menu {
  margin-bottom: $dropdown-spacer;
}

.dropright .dropdown-menu {
  margin-left: $dropdown-spacer;
}

.dropleft .dropdown-menu {
  margin-right: $dropdown-spacer;
}

// Dividers (basically an `<hr>`) within the dropdown
.dropdown-divider {
  @include nav-divider($dropdown-divider-bg);
}

// Links, buttons, and more within the dropdown menu
//
// `<button>`-specific styles are denoted with `// For <button>s`
.dropdown-item {
  padding: $dropdown-item-padding-y $dropdown-item-padding-x;
  font-weight: $font-weight-normal;
  color: $dropdown-link-color;
  font-size: $dropdown-item-font-size;
  transition: $dropdown-item-transition;

  @include hover-focus {
    color: $dropdown-link-hover-color;
    @include gradient-bg($dropdown-link-hover-bg);
  }

  &.active,
  &:active {
    color: $dropdown-link-active-color;
    @include gradient-bg($dropdown-link-active-bg);
  }

  &.disabled,
  &:disabled {
    color: $dropdown-link-disabled-color;

    &:hover {
      background: none;
      cursor: not-allowed;
    }
  }
}

// Dropdown section headers
.dropdown-header {
  padding: $dropdown-padding-y $dropdown-item-padding-x;
  font-size: $font-size-sm;
  color: $dropdown-header-color;
}


// fix dropdown don't shows
.bootstrap-select.btn-group {
  .dropdown-menu.inner {
    display: block;
  }
}
