// include equal hieght mixin
.eq-height {
  @include row-make-equal-height();
}

// include vf-morelink mixin
.vf-more-link {
  @include vf-more-link();
}

.sr-only {
  @extend %sr-only;
}

// include color mixin
@include v-text-color();

// image video to add icon play
.js-img-video {
  > a {
    position: relative;
    width: 100%;
    height: 100%;
    display: block;

    &:hover {
      &:before {
        color: $primary;
      }
    }

    &:before {
      content: $icon-play;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-family: 'vactory';
      color: $white;
      font-size: 64px;
      transition: color .3s;
      z-index: 1;

      .cke_editable & {
        display: none;
      }
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, .3);
      z-index: 0;

      .cke_editable & {
        display: none;
      }
    }

    &.loader {
      &::before {
        content: '' !important;
        background-position: center;
        width: 30px;
        height: 30px;
        background-image: url(../img/loader.gif);
        background-size: contain;
      }
    }
  }
}

// loading global class
.loading {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    background-size: 100px 100px;
    background: $primary url(../img/rings.svg) no-repeat center center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 0;
  }
}

.js-zoom {
  display: block;
}

.qoute{
  position: relative;
  &::before {
    font-family: 'vactory';
    position: absolute;
    font-size: 50px;
    top: 0;
    opacity: .1;
  }

  &-left {
    @extend .qoute;
    &::before {
      content: $icon-quote-left;
      left: 40px;
    }
  }
  &-right {
    @extend .qoute;
    &::before {
      content: $icon-quote-right;
      right: 40px;
    }
  }
}

//class text uppercase
.text-uppercase {
  text-transform: uppercase;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  word-wrap: normal;
}

.invisible {
  visibility: hidden;
}

.hidden {
  display: none;

  body.cke_editable & {
    display: block !important;
  }
}

// hidden mobile
.hidden-mobile {
  @include media-breakpoint-down(sm) {
    display: none !important;

    body.cke_editable & {
      display: block !important;
    }
  }
}

// -----------------------------------------------------------------------------
.container .container {
  padding-left: 0;
  padding-right: 0;
}

.no-container {
  padding-left: 0;
  padding-right: 0;
  max-width: 100%;
}

.last-mb-0 {
  &:last-child {
    margin-bottom: 0 !important;
  }
}

/* -------------------------------------------------------------------------- */
%sr-only,
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.cursor-pointer {
  cursor: pointer;
}